import React from 'react';


function Counter(props) {


    return (
        <div>
            index:{props.index}
            <button onClick={() => props.update(props.index, -1)}>+1</button>
            <button onClick={() => props.update(props.index, -2)}>+2</button>
            <button onClick={() => props.update(props.index, -3)}>+3</button>
            {props.count}
            <button onClick={() => props.update(props.index, 1)}>+1</button>
            <button onClick={() => props.update(props.index, 2)}>+2</button>
            <button onClick={() => props.update(props.index, 3)}>+3</button>
            <button onClick={() => props.reset(props.index)}>Reset</button>
            <button onClick={() => props.delete(props.index)}>Delete</button>

        </div>
    );
}

export default Counter;
