import React, {useState} from 'react';
import Counter from "./Counter";


function App() {

    const [counter, setCounter] = useState([4, 5, 9, 8, 7])

    const addCounter = () => {
        const r = Math.round(Math.random() * 10)
        setCounter([...counter, r])
    }

    const deleteCounters = () => setCounter([])

    const resetAllCounters = () => {
        const newCounters = [...counter].map(el => el = 0)
        setCounter(newCounters)
    }

    const resetCounter = (index) => {
        const newCounter = [...counter]
        newCounter[index] = 0
        setCounter(newCounter)
    }

    const deleteOneCounter =(index)=> {
        const newCounter = [...counter].filter((el, i)=> {
           return i !== index
        })
        setCounter(newCounter)
    }

    const updateCounter = (index, value) => {
        const newCounter = [...counter]
        newCounter[index] += value
        setCounter(newCounter)
    }

    return (

        <div>
            <button onClick={addCounter}>Add counter</button>
            <button onClick={deleteCounters}>Dell counter</button>

            {counter.map((el, i) => (
                <Counter
                    count={el}
                    index={i}
                    key={i}
                    reset={resetCounter}
                    delete={deleteOneCounter}
                    update={updateCounter}
                />
            ))}
            <button onClick={resetAllCounters}>Reset All</button>

        </div>
    );
}

export default App;
